<template>
  <div class="player" id="player" :style="{paddingBottom:type=='index'? '3.2rem' : 0}">
    <div class="head">
      <a :href="goUrl">
        <img src="../../assets/images/return.png" class="back" alt="">
        <img src="../../assets/images/logo.png" class="logo" alt="">
      </a>
      <span>{{title}}</span>
    </div>
    <div class="player-box">
      <div class="video">
        <div class="nostart" v-if="noStart">
          <img style="width: 2rem;" src="../../assets/images/nostart.png" alt="">
        </div>
        <video id="my-player" class="video-js" style="width: 100%;" autoplay :poster="poster"></video>
      </div>
      <div class="line">
        <div class="title">
          <img src="../../assets/images/playerIcon.png" alt="">
          <span>播放源</span>
        </div>
        <div class="line-list">
          <div
            class="line-item"
            v-for="(item,index) in line"
            @click="changeLine(item,index)"
            :class="{active:actLine==index}">
            <a target="_blank" v-if="item.playMode=='iframe'" :href="item.url"></a>
            {{item.title?item.title.substring(0,5):item.title}}
          </div>
          <div v-show="line.length==0" class="noline">暂时没有播放源，请稍后再试</div>
        </div>
      </div>
    </div>

    <!--直播广告位-->
    <template v-if="type=='index'&&showBanner" >
      <div class="banner"></div>
    </template>

    <!--电视节目单-->
    <template v-if="type=='tv'">
      <div class="tv-line" v-if="tv.length>0">
        <div class="title">
          <img src="../../assets/images/show.png" alt="">
          <span>节目单</span>
        </div>
        <div class="tv-list">
          <div v-for="(item,index) in tv" :class="{active: actTv==index}" class="tv-item">
            <span class="time">{{getTimeForm(item.beginTime)}}</span>
            <span class="name">{{item.programName}}</span>
            <span v-if="actTv==index" class="status">播放中</span>
          </div>
          <div class="loading" v-show="loading">
            <img src="../../assets/images/loading.gif" alt="">
            <span>正在努力加载中…</span>
          </div>
          <div v-if="nomore" class="nomore">没有更多数据了…</div>
        </div>
      </div>
    </template>
    <!--录像-->
    <template v-if="type=='review'">
      <div class="review" v-if="review.length>0">
        <div class="title">
          <img src="../../assets/images/like.png" alt="">
          <span>推荐</span>
        </div>
        <div class="review-list">
          <a class="review-item" v-for="(item,index) in review" :key="index" target="_blank" :href="'player.html?matchId='+item.gameId+'&type=review'+'&category='+category+'&title='+(item.homeName+'VS'+item.awayName)+'&backplay=1'">
            <img src="../../assets/images/review.png" alt="">
            <div class="title">
              <div class="title-content"><span>{{item.homeName}}VS{{item.awayName}}</span></div>
              <span class="title">{{item.beginTime?item.beginTime.split(' ')[0] : item.beginTime}}</span>
            </div>
          </a>
          <div class="loading" v-show="loading">
            <img src="../../assets/images/loading.gif" alt="">
            <span>正在努力加载中…</span>
          </div>
          <div v-if="nomore" class="nomore">没有更多数据了…</div>
        </div>
      </div>
    </template>
  </div>
</template>
<style lang="less">
  @import "css/player.less";
</style>
<script>
  import poster from '../../assets/images/poster.png'
  export default {
    name: 'player',
    data () {
      return {
        type: '',
        line: [],
        actLine: -1,
        tv: [],
        actTv: 0,
        review: [],
        actReview: 0,
        gameId: 0,
        pageNum: 1,
        pageSize: 16,
        category: '',
        showBanner: false,
        scrollState: true,
        nomore: false,
        nodata: false,
        loading: false,
        poster: poster,
        playerOptions:{
          height:300,
          autoplay: false, // 自动播放
          controls: true, // 是否显示控制栏
          techOrder: ['flash', 'html5'], // 兼容顺序
          sourceOrder: true, //
          flash: { hls: { withCredentials: false } },
          html5: { hls: { withCredentials: false } },
          sources: [],
          poster: this.poster // 播放器默认图片
        },
        player:null,
        title: '',
        noStart: false,
        goUrl: 'index.html'
      }
    },
    mounted () {
      this.type = this.$utils.getPara('type')
      this.gameId = this.$utils.getPara('matchId')
      this.category = this.$utils.getPara('category')
      this.title = this.$utils.getPara('title')
      this.title = this.title ? this.title : '低调看球'
      if (this.type == 'index') {
        this.getLiveList()
      } else if (this.type == 'tv') {
        this.getTvList()
        this.getTvShow()
      } else if (this.type == 'review'){
        this.getLiveList()
        this.getReviewList()
      }
      if (this.type == 'tv') {
        this.scrollMore()
      }
    },
    methods: {
      initPlay(){
        if ( this.line.length > 0 && this.line[this.actLine].playMode != 'iframe') {
          if(this.player != null){
            this.player.src({src:this.line[this.actLine].url})
            this.player.play()
          } else {
            this.playerOptions.sources = [{src: this.line[this.actLine].url}]
            this.$nextTick(()=>{
              this.player = videojs('my-player', this.playerOptions, function onPlayerReady() {
                videojs.log('Your player is ready!');
              });
              this.player.on('error', function (e) {
                console.error('VIDEO ERROR',e)
                this.player.push()
              })
            })
          }
        }
      },
      changeLine (item,idx) {
        if (item.playMode != 'iframe') {
          this.actLine = idx
          this.initPlay();
        }
      },
      getLiveList() {
        // 获取直播线路 / 录像香炉
        this.$axios.get(`live-line/game/${this.gameId}`)
          .then(res=>{
            res = res.data
            if (res.code == 200) {
              this.line = res.data;
              this.actLine = 0;
              this.initPlay()
              if (this.line.length==0 && (this.$utils.getPara('status') == 1 || this.$utils.getPara('status')==4)) {
                this.noStart = true
              }
            } else {
              console.error(res)
            }
          })
      },
      getTvList () {// 获取电视节目
        this.$axios.get(`live-line/tvstation/${this.gameId}`)
          .then(res=>{
            res = res.data
            if (res.code == 200) {
              this.line = res.data;
              this.actLine = 0;
              this.initPlay()
            } else {
              console.error(res)
            }
          })
      },
      getTvShow(pageNum) { // 获取节目单
        if (pageNum > 1) {
          this.loading = true
        }
        this.$axios.get('tvstation/program',{params: {
          pageNum: pageNum,
          pageSize: this.pageSize,
          tvid: this.gameId
        }}).then(res => {
          this.scrollState = true
          this.loading = false
          res = res.data
          if (res.code == 200) {
            let list = res.data
            if (pageNum==1) {
              this.tv = list
              if ( this.tv.length ==0) {
                this.nodata = true
              } else if (list.length < this.pageSize) {
                this.nomore = true
              }
            }else{
              this.tv.push(...list)
              if (list.length < this.pageSize) {
                this.nomore = true
              }
            }
          }
        })
      },
      getReviewList (pageNum = 1) { // 获取推荐
        if (pageNum > 1) {
          this.loading = true
        }
        this.$axios.get('game/play-back',{params:{pageNum:pageNum,pageSize:this.pageSize,category:this.category}})
          .then(res => {
            this.scrollState = true
            this.loading = false
            res = res.data
            if (res.code == 200) {
              let list = res.data
              if (pageNum==1) {
                this.review = list
                if ( this.review.length ==0) {
                  this.nodata = true
                } else if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }else{
                this.review.push(...list)
                if (list.length < this.pageSize) {
                  this.nomore = true
                }
              }
            }
          })
      },
      scrollMore() {
        this.$nextTick(()=>{
          let box = document.getElementById('player')
          box.addEventListener('scroll',()=>{
            let temp = box.scrollTop + box.offsetHeight
            if (temp + 100 >= box.scrollHeight) {
              if ((!this.nomore) && this.scrollState) {
                this.scrollState = false
                this.pageNum++
                if (this.type == 'tv') {
                  this.getTvShow(this.pageNum)
                } else if (this.type == 'review'){
                  this.getReviewList(this.pageNum)
                }
              }
            }
          })
        })
      },
      getTimeForm(time) {
        if (time) {
          time = time.replace(/-/g,'/')
          time = new Date(time)
          let h = time.getHours()
          let m = time.getMinutes()
          h = (h < 10 ? '0' + h : h)
          m = (m < 10 ? '0' + m : m)
          return h + ':' + m
        } else {
          return time
        }
      },
    },
  }
</script>



